<template>
  <div class="shop-oreder-home">
    <div @touchmove.prevent class="chain-tab">
      <div class="chain-tab-ul">
        <div class="chain-tab-li">
          <router-link :to="'untreatedOrder'" replace>
            <div class="chain-tab-li-hot">
              <span class="chain-name">未处理订单</span>
            </div>
          </router-link>
        </div>
        <div class="chain-tab-li">
          <router-link :to="'processedOrder'" replace>
            <div class="chain-tab-li-hot">
              <span class="chain-name">已处理订单</span>
            </div>
          </router-link>
        </div>
      </div>
    </div>
    <keep-alive>
        <router-view ref="orderView" class="chain-tab-view" @touchstart.native="scrollFixStart" @touchmove.native="scrollFixMove"></router-view>
    </keep-alive>
  </div>
</template>

<script>
import utils from "../../../../common/js/utils.js";
import { mapState } from "vuex";
import Vue from "vue";
import { Toast } from "mint-ui";
import { MessageBox } from "../../../popupBox/messageBox/message-box.js";
import "mint-ui/lib/toast/style.css";
Vue.component(MessageBox.name, MessageBox);
Vue.component(Toast.name, Toast);

export default {
  name: "shop-oreder-home",
  data() {
    return {
      untreatedData: null,
      processedData: null
    };
  },
  components: {},
  created() {},
  beforeRouteLeave(to, from, next) {
    MessageBox.close();
    next();
  },
  computed: {
    ...mapState(["userid", "path", "role"])
  },
  methods: {
    scrollFixStart(e) {
      utils.scrollFixStart(e);
    },
    scrollFixMove(e) {
      utils.scrollFixMove(e);
    },
    markToProcessed(cellData){
        var spanLeft1='<span style="font-weight:700;color:#000;">';
        var spanRight='</span>';  
        MessageBox.confirm('标记',{title: '标记',
            message: "是否将"+spanLeft1+"\'订单"+cellData.id+"\'"+spanRight+"标记为已处理订单？",
            showCancelButton: true}).
            then(action => {
                this.requestProcessed(cellData);
            }).catch(response=>{
                //请求出错处理
                console.log(response);
            });
    },
    requestProcessed(cellData){
      this.$store.dispatch("toggleLoading", true);
      this.$http
      .post(this.path + `php/Route.php`, {
          type:'handleagentgoods',
          aid:this.userid,
          buyid:cellData.id
      })
      .then(response => {
        this.$store.dispatch("toggleLoading", false);
        let resultData = response.body;
        if(resultData.error=="ok"){
          let index=this.untreatedData.indexOf(cellData);
          if(index>=0){
              this.untreatedData.splice(index,1);
              if(this.$refs.orderView.wetherShowEmpty) this.$refs.orderView.wetherShowEmpty();
          } 
          cellData.state="5";
          if(this.processedData) this.processedData.unshift(cellData);
          Toast({message: "操作成功",iconClass: "iconfont icon-tishi1",duration: 1000});
        }else{
          Toast({message: "请求失败",iconClass: "iconfont icon-tishi1",duration: 1000});
        }
      })
      .catch(response=>{
        //请求出错处理
        this.$store.dispatch("toggleLoading", false);
      });
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
.shop-oreder-home {
    font-family: 'Microsoft YaHei';
    width: 100%;
    padding-top: 0.66rem;
    box-sizing: border-box;
    flex: 1;
    display: flex;
    flex-direction: column;

    .chain-tab-view {
        box-sizing: content-box;
        padding-top: 0.1rem;
        flex: 1;
        overflow: auto;
        overflow-x: hidden;
    }

    .chain-tab {
        position: fixed;
        top: 0.3rem;
        height: 0.36rem;
        background-color: #fff;
        width: 100%;
        z-index: 10;

        .chain-tab-ul {
            margin: 0px auto;
            height: 100%;
            width: 80%;
            display: flex;
            align-items: center;
            text-align: center;

            .chain-tab-li {
                overflow: hidden;
                height: 100%;
                flex: 1;

                .chain-tab-li-hot {
                    width: 100%;
                    height: 100%;
                    position: relative;

                    .chain-name {
                        line-height: 0.36rem;
                        color: #42495C;
                        font-size: 0.12rem;
                        position: relative;
                        top: 0.01rem;
                        letter-spacing: 0.01rem;
                    }
                }

                .chain-tab-li-hot::after {
                    position: absolute;
                    bottom: -3px;
                    left: 15%;
                    width: 70%;
                    height: 2px;
                    background: #FD8709;
                    border-radius: 1px;
                    content: '';
                    -webkit-transition: -webkit-transform 0.2s;
                    transition: transform 0.2s;
                    -webkit-transform: translate3d(0, 0, 0);
                    transform: translate3d(0, 0, 0);
                }

                .active {
                    .chain-name {
                        top: 0px;
                        color: #FD8709;
                    }

                    .chain-tab-li-hot::after {
                        -webkit-transform: translate3d(0, -150%, 0);
                        transform: translate3d(0, -150%, 0);
                    }
                }
            }
        }
    }
}
</style>



